import React from 'react';

export function Booking(props) {
    return (
        <section className="section-book">
            <div className="book">
                <div className="book__form">
                    <form action="#" className="form">

                        <div className="u-margin-bottom-medium">
                            <h2 className="heading-secondary">
                                Start booking now
                            </h2>
                        </div>
                        <div className="form__group">
                            <input id="name" type="text" className="form__input" placeholder="Full Name" required />
                            <label htmlFor="name" className="form__label">Full name</label>
                        </div>
                        <div className="form__group">
                            <input id="email" type="email" className="form__input" placeholder="Email" required />
                            <label htmlFor="email" className="form__label">Email</label>
                        </div>

                        <div className="form__group">
                            <div className="form__radio-group">
                                <input name="size" type="radio" className="form__radio-input" id="small" />
                                <label htmlFor="small" className="form__radio-label">
                                    <span className="form__radio-button"></span>
                                    Small Tour Group</label>
                            </div>
                            <div className="form__radio-group">
                                <input name="size" type="radio" className="form__radio-input" id="large" />
                                <label htmlFor="large" className="form__radio-label">
                                    <span className="form__radio-button"></span>
                                    Large Tour Group</label>
                            </div>

                            <div className="form__group u-margin-top-medium">
                                <button className="btn btn--green">Next Step &rarr;</button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </section>
    );
}