import React from 'react';

export function Testimonial({personName, image}) {
    return (
        <div className="story">
        <figure className="story__shape">
            <img src={image} alt="Person" className="story__image" />
            <figcaption className="story__caption">
               {personName}
            </figcaption>
        </figure>

        <div className="story__text">
            <h3 className="heading-tertiary u-margin-bottom-small">
                I had the best week with my family!
            </h3>

            <p className="paragraph">
                Lorem ipsum, dolor sit amet consectetur adipisicing elit. Corrupti veritatis, ullam in odio vitae assumenda autem laboriosam sequi iure incidunt, tenetur minus dicta itaque aperiam? Ipsa exercitationem consequatur temporibus placeat.
            </p>
        </div>
    </div>
    )
}