import React from 'react';
import { Testimonial } from '../../components';
import Person1 from '../../assets/nat-8.jpg';
import Person2 from '../../assets/nat-9.jpg';
import Video1 from '../../assets/video.mp4';
import Video2 from '../../assets/video.webm';

export function Testimonials(props) {

    return (
        <section className="section-stories">

            <div className="bg-video">
                <video className="bg-video__content" autoPlay={true} muted={true} loop={true}>
                    <source src={Video1} type="video/mp4" />
                    <source src={Video2} type="video/webm" />
                    Your browser does not support video formats!
                </video>
            </div>
            <div className="u-center-text u-margin-bottom-large">
                <h2 className="heading-secondary">
                    We make people genuinely happy!
                </h2>
            </div>

            <div className="row">
                <Testimonial personName="Mary Smith" image={Person1} />
                <Testimonial personName="John Smith" image={Person2} />

                <div className="u-center-text u-margin-top-medium">
                    <a href="#" className="btn-text">
                        Read all stories &rarr;
                    </a>
                </div>
            </div>
        </section>
    )
}