import React from 'react';
import Logo from '../../assets/logo-green-2x.png';

export function Footer(props) {
    return (
        <footer className="footer">
            <div className="footer__logo-box u-margin-bottom-medium">
                <img src={Logo} alt="Logo for natours" className="footer__logo" />
            </div>
            <div className="row">
                <div className="col-1-of-2">
                    <div className="footer__navigation">
                        <ul className="footer__list">
                            <li className="footer__item"><a href="#" className="footer__link">Company</a></li>
                            <li className="footer__item"><a href="#" className="footer__link">Contact</a></li>
                            <li className="footer__item"><a href="#" className="footer__link">Careers</a></li>
                            <li className="footer__item"><a href="#" className="footer__link">Privacy</a></li>
                            <li className="footer__item"><a href="#" className="footer__link">Terms</a></li>
                        </ul>
                    </div>
                </div>
                <div className="col-1-of-2">
                <div className="footer__navigation">
                    <p className="footer__copyright">
                        A demo website cloned from scratch by <a href="https://github.com/ravic0" target="_blank" className="footer__link">Ravnit Sehgal</a>. Written with &#10084; using React and SCSS. Original design - Jonas Schmedtmann.
                    </p>
                </div> 
                </div>
            </div>
        </footer>
    )
}