import React from 'react';
import Tour1 from '../../assets/nat-8.jpg';
import Tour2 from '../../assets/nat-9.jpg';

export function Popup(props) {

    return (
        <div className="popup" id="popup">
            <div className="popup__content">
                <div className="popup__left">
                    <img src={Tour1} alt="Tour Photo 1" className="popup__img" />
                    <img src={Tour2} alt="Tour Photo 2" className="popup__img"/>
                </div>
                <div className="popup__right">
                    <a href="#section-tours" className="popup__close">&times;</a>
                    <h2 className="heading-secondary u-margin-bottom-small"> Start Booking Now</h2>
                    <h3 className="heading-tertiary u-margin-bottom-small">Important &ndash; Please read these terms before booking</h3>
                    <p className="popup__text">
                        Lorem ipsum dolor sit amet consectetur adipisicing elit. Voluptatum excepturi, asperiores, pariatur ipsa optio ut commodi architecto accusantium modi earum aliquid ratione dicta natus impedit sapiente harum quia maiores deleniti.
                    consectetur adipisicing elit. Ducimus, placeat, incidunt earum quis dolores totam voluptates dignissimos, consectetur minima accusamus non quos. Perferendis temporibus odit quia soluta numquam harum esse.
                   sit amet consectetur, adipisicing elit. Praesentium impedit quod magni veritatis perferendis quas magnam? Soluta eos, optio modi, deserunt consequatur provident eveniet labore quae, impedit neque alias hic.
                    </p>
                    <a href="#" className="btn btn--green">Book Now</a>
                </div>
            </div>
        </div>
    )
}