import '../../sass/main.scss';
import Logo from '../../assets/logo-white.png'

// import { Grid}  from '../Grid';

export function Header(props) {

    return (
      <>
        <header className="header">
      <div className="header__logo-box">
        <img className="header__logo" src={Logo} alt="Logo of the website" />
      </div>

      <div className="header__text-box">
        <h1 className="heading-primary">
          <span className={`heading-primary--main ${!props.showModal? 'heading-primary--main-animated' : ''}`}>Outdoors</span>
          <span className={`heading-primary--sub ${!props.showModal? 'heading-primary--sub-animated' : ''}`}>Is where the magic happens</span>
        </h1>

        <a className={`btn btn--white ${!props.showModal ? 'btn--animated':''}`} href="#">
          Discover Tours
        </a>
      </div>

    </header>

    {/* <Grid /> */}
    </>
    )
}