
import React, {useState} from 'react';
import { Introduction, Popup } from './components';
import { Header, Main, Footer, Navigation } from './layouts';




function App() {
  // console.log(process.env);
  const envCheck = process.env.NODE_ENV !== 'production';
  // console.log(envCheck);
  const [showModal, toggleModal] = useState(envCheck);

  function close() {
    toggleModal(false)
  }


  return (<>
    {showModal ? <Introduction close={close} /> : null}
    <Navigation />
    <Header showModal={showModal}/>
    <Main />
    <Footer />
    <Popup />
  </>
  );
}

export default App;
